.gardening-services {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(135deg, #2d5a27 0%, #4a7c59 25%, #6b8e23 50%, #8fbc8f 75%, #98fb98 100%);
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  overflow-x: hidden;
}

.services-header {
  text-align: center;
  margin-bottom: 50px;
  color: #fff;
  max-width: 800px;
  padding: 0 20px;
}

.services-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 15px;
  text-shadow: 0 2px 4px rgba(0,0,0,0.3);
  line-height: 1.2;
}

.services-header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin: 0;
  line-height: 1.5;
}

.services-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
  max-width: 1200px;
  width: 100%;
  padding: 0 20px;
}

.service-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-height: 400px;
  display: flex;
  flex-direction: column;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}

.service-badge {
  position: absolute;
  top: 15px;
  right: 15px;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  padding: 8px 15px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(76, 175, 80, 0.3);
  white-space: nowrap;
}

.service-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  text-align: center;
}

.service-card h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #2d5a27;
  margin-bottom: 15px;
  text-align: center;
  line-height: 1.3;
}

.service-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 0.95rem;
  flex-grow: 1;
}

.service-features {
  list-style: none;
  padding: 0;
  margin-bottom: 25px;
}

.service-features li {
  color: #2d5a27;
  margin-bottom: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  padding-left: 5px;
}

.service-button {
  width: 100%;
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
  border: none;
  padding: 15px 20px;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.3);
  margin-top: auto;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.service-button:hover {
  background: linear-gradient(45deg, #45a049, #4CAF50);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(76, 175, 80, 0.4);
}

.service-button:active {
  transform: translateY(0);
}

/* 팝업 스타일 */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
  padding: 20px;
}

.popup-content {
  background: white;
  border-radius: 20px;
  padding: 30px;
  max-width: 400px;
  width: 100%;
  text-align: center;
  position: relative;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: popupSlideIn 0.3s ease-out;
  max-height: 90vh;
  overflow-y: auto;
}

@keyframes popupSlideIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.popup-close {
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  font-size: 2rem;
  color: #999;
  cursor: pointer;
  transition: color 0.3s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.popup-close:hover {
  color: #333;
  background: rgba(0, 0, 0, 0.1);
}

.popup-image {
  margin-bottom: 20px;
}

.popup-image img {
  width: 100%;
  max-width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.popup-text h3 {
  color: #2d5a27;
  font-size: 1.3rem;
  margin-bottom: 10px;
  font-weight: 600;
}

.popup-text p {
  color: #666;
  margin-bottom: 8px;
  line-height: 1.5;
}

/* 반응형 디자인 */
@media (max-width: 1024px) {
  .services-container {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 25px;
  }
  
  .service-card {
    min-height: 380px;
  }
}

@media (max-width: 768px) {
  .gardening-services {
    padding: 30px 15px;
  }
  
  .services-header {
    margin-bottom: 40px;
    padding: 0 10px;
  }
  
  .services-header h1 {
    font-size: 2rem;
    margin-bottom: 12px;
  }
  
  .services-header p {
    font-size: 1rem;
  }
  
  .services-container {
    grid-template-columns: 1fr;
    gap: 20px;
    padding: 0 10px;
  }
  
  .service-card {
    padding: 25px;
    min-height: 350px;
  }
  
  .service-icon {
    font-size: 2.5rem;
    margin-bottom: 15px;
  }
  
  .service-card h2 {
    font-size: 1.3rem;
    margin-bottom: 12px;
  }
  
  .service-description {
    font-size: 0.9rem;
    margin-bottom: 15px;
  }
  
  .service-features li {
    font-size: 0.85rem;
    margin-bottom: 6px;
  }
  
  .service-button {
    padding: 12px 16px;
    font-size: 0.95rem;
    min-height: 45px;
  }
  
  .service-badge {
    font-size: 0.8rem;
    padding: 6px 12px;
  }
  
  .popup-content {
    padding: 25px;
    margin: 10px;
  }
  
  .popup-close {
    top: 10px;
    right: 15px;
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .gardening-services {
    padding: 0 !important;
    min-height: 100vh;
  }
  .services-header {
    margin-bottom: 10px !important;
    padding: 0 5px !important;
  }
  .services-header h1 {
    font-size: 1.2rem !important;
    margin-bottom: 4px !important;
  }
  .services-header p {
    font-size: 0.85rem !important;
  }
  .services-container {
    display: grid !important;
    grid-template-columns: 1fr 1fr !important;
    gap: 6px !important;
    padding: 0 !important;
    width: 100vw !important;
    max-width: 100vw !important;
  }
  .service-card {
    padding: 4px 2px !important;
    min-height: unset !important;
    border-radius: 0 !important;
    background: transparent !important;
    box-shadow: none !important;
    border: none !important;
    margin: 0 !important;
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
  }
  .service-icon {
    font-size: 1.3rem !important;
    margin-bottom: 4px !important;
  }
  .service-card h2 {
    font-size: 0.95rem !important;
    margin-bottom: 2px !important;
  }
  .service-description {
    font-size: 0.7rem !important;
    margin-bottom: 4px !important;
  }
  .service-features {
    margin-bottom: 4px !important;
  }
  .service-features li {
    font-size: 0.65rem !important;
    margin-bottom: 2px !important;
  }
  .service-button {
    padding: 6px 0 !important;
    font-size: 0.75rem !important;
    min-height: 28px !important;
    border-radius: 4px !important;
    margin-top: 4px !important;
  }
  .service-badge {
    font-size: 0.6rem !important;
    padding: 2px 4px !important;
    top: 3px !important;
    right: 3px !important;
    border-radius: 6px !important;
  }
  .popup-overlay {
    padding: 0 !important;
  }
  .popup-content {
    padding: 0 !important;
    margin: 0 !important;
    border-radius: 0 !important;
    box-shadow: none !important;
    max-width: 100vw !important;
    width: 100vw !important;
  }
  .popup-close {
    top: 8px !important;
    right: 8px !important;
    font-size: 1.2rem !important;
    width: 28px !important;
    height: 28px !important;
  }
  .popup-image img {
    max-width: 90vw !important;
    border-radius: 0 !important;
    box-shadow: none !important;
  }
  .popup-text h3 {
    font-size: 1rem !important;
  }
  .popup-text p {
    font-size: 0.85rem !important;
  }
}

@media (max-width: 360px) {
  .gardening-services {
    padding: 15px 8px;
  }
  
  .services-header h1 {
    font-size: 1.6rem;
  }
  
  .services-header p {
    font-size: 0.9rem;
  }
  
  .service-card {
    padding: 18px;
    min-height: 300px;
  }
  
  .service-icon {
    font-size: 2rem;
  }
  
  .service-card h2 {
    font-size: 1.1rem;
  }
  
  .service-description {
    font-size: 0.8rem;
  }
  
  .service-features li {
    font-size: 0.75rem;
  }
  
  .service-button {
    padding: 8px 12px;
    font-size: 0.85rem;
    min-height: 38px;
  }
  
  .service-badge {
    font-size: 0.7rem;
    padding: 4px 8px;
  }
}

/* 터치 디바이스 최적화 */
@media (hover: none) and (pointer: coarse) {
  .service-card:hover {
    transform: none;
  }
  
  .service-button:hover {
    transform: none;
  }
  
  .service-button:active {
    transform: scale(0.98);
  }
  
  .popup-close:active {
    background: rgba(0, 0, 0, 0.2);
  }
}

/* 고해상도 디스플레이 최적화 */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .service-card {
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  }
  
  .service-button {
    box-shadow: 0 2px 8px rgba(76, 175, 80, 0.2);
  }
} 